import { InitGPU, CreateGPUBufferUint, CreateViewProjection, CreateAnimation } from './helper';
import shader from './shader.wgsl';
import { CubeData } from './vertex_data';
import { vec3, mat4 } from 'gl-matrix';

//TODO: implement camera controls
var createCamera = require('3d-view-controls')
var perspective = require('gl-mat4/perspective')

const Create3DObject = async (isAnimation = false) => {

    //initialize canvas and get logical device
    const gpu = await InitGPU();
    const device = gpu.device;

    //get vertex data from vertex_data
    const cubeData = CubeData();
    const numberOfVertices = cubeData.indexData.length;
    // const vertexBuffer = CreateGPUBuffer(device, cubeData.vertexData);
    const vertexBuffer = device.createBuffer({
        size: cubeData.vertexData.byteLength,
        usage: GPUBufferUsage.VERTEX | GPUBufferUsage.COPY_DST,
        mappedAtCreation: true
    });
    //TODO: investigate these two lines
    new Float32Array(vertexBuffer.getMappedRange()).set(cubeData.vertexData);
    vertexBuffer.unmap();

    const indexBuffer = CreateGPUBufferUint(device, cubeData.indexData);

    //create shader module from shader code
    const shaderModule = device.createShaderModule({ code: shader });
 
    //create render pipeline
    const pipeline = device.createRenderPipeline({
        layout:'auto',
        vertex: {
            module: shaderModule,
            entryPoint: "vs_main",
            buffers:[
                {
                    arrayStride: 24,
                    attributes: [
                        {
                            shaderLocation: 0,
                            format: "float32x3",
                            offset: 0
                        },
                        {
                            shaderLocation: 1,
                            format: "float32x3",
                            offset: 12
                        }
                    ]
                }
            ]
        },
        fragment: {
            module: device.createShaderModule({                    
                code: shader
            }),
            entryPoint: "fs_main",
            targets: [
                {
                    format: gpu.format
                }
            ]
        },
        primitive:{
            topology: "triangle-list",
        },
        depthStencil:{
            format: "depth24plus",
            depthWriteEnabled: true,
            depthCompare: "less"
        }
    });

    // create uniform data
    const nx = 16;
    const ny = 16;
    const nz = 4;
    const ni = nx * ny * nz;  // number of instances

    const matrixSize = 4 * 16;
    const uniformBufferSize = ni * matrixSize;

    let rotation = vec3.fromValues(0, 0, 0);  

    const uniformBuffer = device.createBuffer({
        size: uniformBufferSize,
        usage: GPUBufferUsage.UNIFORM | GPUBufferUsage.COPY_DST,
    });

    const uniformBindGroup = device.createBindGroup({
        layout: pipeline.getBindGroupLayout(0),
        entries: [{
            binding: 0,
            resource: {
                buffer: uniformBuffer
            }
        }]
    });

    const vp = CreateViewProjection(gpu.canvas.width/gpu.canvas.height);
    const projectMatrix = vp.projectionMatrix;

    const modelMat = new Array(ni);
    const mvpMatricesData = new Float32Array(16 * ni);
    
    let i = 0;
    for(let x = 0; x < nx; x++){
        for(let y = 0; y < ny; y++){
            for(let z = 0; z < nz; z++){
                modelMat[i] = mat4.create();
                mat4.translate(
                    modelMat[i], 
                    modelMat[i], 
                    vec3.fromValues(
                        4*(x - nx/2 + 0.5), 
                        4*(y - ny/2 + 0.5), 
                        4*(z - nz/2 + 0.5)
                    )
                );
                i++;
            }
        }
    }

    const viewMatrix = mat4.create();
    mat4.translate(viewMatrix, viewMatrix, vec3.fromValues(0, 0, -50));
    const tmpMat = mat4.create();

    let textureView = gpu.context.getCurrentTexture().createView();
    const depthTexture = device.createTexture({
        size: [gpu.canvas.width, gpu.canvas.height, 1],
        format: "depth24plus",
        usage: GPUTextureUsage.RENDER_ATTACHMENT
    });
    const renderPassDescription = {
        colorAttachments: [{
            view: textureView,
            clearValue: { r: 0.2, g: 0.247, b: 0.314, a: 1.0  }, //background color
            loadOp: "clear",
            storeOp: 'store'
        }],
        depthStencilAttachment: {
            view: depthTexture.createView(),
            depthClearValue: 1.0,
            depthLoadOp: 'clear',
            depthStoreOp: "store",
        }
    };
    
    function draw() {
        let m = 0, i = 0;
        for(let x = 0; x < nx; x++){
            for(let y = 0; y < ny; y++){
                for(let z = 0; z < nz; z++){
                    mat4.rotate(
                        tmpMat,
                        modelMat[i],
                        1,
                        vec3.fromValues(
                            Math.sin((x+0.5)*2*rotation[0]), 
                            Math.cos((y+0.5)*2*rotation[1]), 
                            Math.sin(2*rotation[2])*Math.cos(2*rotation[2])
                        )
                    );
                    mat4.multiply(tmpMat, viewMatrix, tmpMat);
                    mat4.multiply(tmpMat, vp.projectionMatrix, tmpMat);

                    mvpMatricesData.set(tmpMat, m);
                    i++;
                    m += 16;
                }
            }
        }

        //send uniform buffer to the GPU buffer
        device.queue.writeBuffer(
            uniformBuffer,
            0,
            mvpMatricesData.buffer,
            mvpMatricesData.byteOffset,
            mvpMatricesData.byteLength
        );

        textureView = gpu.context.getCurrentTexture().createView();
        renderPassDescription.colorAttachments[0].view = textureView;
        const commandEncoder = device.createCommandEncoder();
        const renderPass = commandEncoder.beginRenderPass(renderPassDescription as GPURenderPassDescriptor);

        renderPass.setPipeline(pipeline);
        renderPass.setBindGroup(0, uniformBindGroup);
        renderPass.setVertexBuffer(0, vertexBuffer);
        renderPass.setIndexBuffer(indexBuffer, 'uint32');
        renderPass.drawIndexed(numberOfVertices, ni);

        renderPass.end();

        device.queue.submit([commandEncoder.finish()]);
    }

    CreateAnimation(draw, rotation, isAnimation);
}

Create3DObject();

window.addEventListener('resize', function(){
    Create3DObject();
});